import { Button, Typography } from "@mui/material";
import React from "react";
import NewsImg from './news-image.png';
import { motion } from "framer-motion";


function Liquidity(){

    return(
        <motion.div
         className="liquidity"
         initial={{opacity:0,scale:0}}
         whileInView={{opacity:1,scale:1}}
         transition={{duration:0.5}}
         viewport={{once:true}}
         >
            <div className="left-liquidity">
                <h1>
                    Never-Ending <br></br>
                    <span className="liq-txt">Liquidity</span>
                </h1>
                <Typography variant="p">
                    Cryptonian instantly analyzes thousands of quotes and fees across multiple DEXes to provide users with the best rates.
                </Typography>
                <Button variant="outlined" size="large" style={{margin:'20px 0px'}}>
                    Swap
                </Button>
            </div>

            <div className="right-liquidity">
                <img src={NewsImg} className="right-liq-img" alt="News-img"></img>
            </div>
        </motion.div>
    )
}


export default Liquidity;