import { Button } from "@mui/material";
import DigitalImg from "./digitalArt.png"
import { motion } from "framer-motion"


function Currency(){

    return(
        <motion.div
         className="currency"
          initial={{opacity:0,scale:0}}
          whileInView={{opacity:1,scale:1}}
          transition={{duration:0.6}}
          viewport={{once:true}}
         >
            <div className="intro-box-left">
                <img src={DigitalImg} alt="digital-art"/>
            </div>
            <div className="intro-box-right">
                <h2>Market Trends: Analyzing the latest trends in <span style={{color:'#00ff8d'}}>cryptocurrency</span> markets.</h2>
                <span className="btn-box">
                    <Button variant="contained" size="large" style={{textTransform:'none',background:'#0db067',fontSize:'18px'}}> Discover now</Button>
                </span>
            </div>
        </motion.div>
    )
}


export default Currency;

