import { Card, Typography } from "@mui/material";
import { useEffect, useState } from "react";

// fetch("https://api.coinstats.app/public/v1/news?skip=0&limit=10")

function About() {
  const [news, setNews] = useState([]);

  var requestOptions = {
    method: "GET",
    redirect: "follow",
  };

  useEffect(() => {
    getNews();
  }, []);

  const getNews = async () => {
    const result = await fetch(
      "https://api.coinstats.app/public/v1/news?skip=0&limit=5",
      requestOptions
    ).then((resp) => {
      return resp.json();
    });

    // console.log(result)
    setNews(result.news);
    console.log(news);
  };

  return (
    <div className="about">
      <h1>Crypto News</h1>

      <div className="news-list">
        {news.map((item, index) => {
          return (
            <Card
              style={{
                background:
                  "linear-gradient(135deg,rgba(255, 255, 255, 0.16),rgba(255, 255, 255, 0.041))",
                backdropFilter: "blur(10px)",
                webkitBackdropFilter: "blur(10px)",
              }}
              className="news-card"
            >
              <div className="news-img"><img src={item.imgURL} alt="news-img"></img></div>

              <div className="news-details">
                <h2>{item.title}</h2>
                <div>
                  <Typography variant="p" fontSize='14px' color='aliceblue'>{item.description}</Typography>
                </div>
                <div className="know-more">
                  <a href={item.link}>
                    Know more
                  </a>
                </div>
              </div>
            </Card>
          );
        })}
      </div>
    </div>
  );
}

export default About;
