import { Button, Typography } from "@mui/material";
import createGlobe from "cobe";
import { useEffect, useRef } from "react";
import { motion } from "framer-motion"



function Head(){
    const canvasRef = useRef();

  useEffect(() => {
    let phi = 0;

    const globe = createGlobe(canvasRef.current, {
      devicePixelRatio: 2,
      width: 810 * 2,
      height: 400*3 ,
      phi: 0,
      theta: 0,
      dark: .8,
      diffuse: 1.2,
      mapSamples: 16000,
      mapBrightness: 1,
      baseColor: [1,0,1],
      markerColor: [0,0,0],
      glowColor: [1,1,1],
      markers: [
        // longitude latitude
        { location: [37.7595, -122.4367], size: 0.03 },
        { location: [40.7128, -74.006], size: 0.1 }
      ],
      onRender: (state) => {
        state.phi = phi;
        phi += 0.007;
      }
    });

    return () => {
      globe.destroy();
    };
  }, []);


    return(
        <motion.div 
          className="header"
          initial={{opacity:0,scale:0}}
          whileInView={{opacity:1,scale:1}}
          transition={{duration:0.6}}
          viewport={{once:true}}  
          >
            <div className="left">
                <h1>Unlock the Power of Blockchain with Cryptocurrency</h1>
                <p>Cryptocurrency is a revolutionary digital asset that has disrupted traditional finance systems. It is a decentralized form of currency that allows for secure and transparent transactions without the need for intermediaries like banks or governments.</p>
                <Button variant="contained" color="secondary" size="large" style={{textTransform:'none',fontSize:'17px'}}>Find more</Button>
            </div>

            <div className="right">
                <canvas
                 className='globe'
                 ref={canvasRef}
                 style={{ width: 400, height: 600, maxWidth: "100%", aspectRatio: 2 }}
                />
            </div>
        </motion.div>
    )
}


export default Head