import { Button, Typography } from "@mui/material";
import React from "react";
import { useState,useEffect} from "react";
import { IconButton } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';

function Search(){
    const [input,setInput] = useState('');
    const [data,setData] = useState([]);


    useEffect(()=>{
        getData()
    },[])

   async function getData(){
    let result = await fetch('https://api.coinstats.app/public/v1/coins?skip=0&limit=50&currency=EUR')
                .then((resp) => {
                    return resp.json();
                })
    // console.log(result)
    setData(result.coins)
    console.log("search data : ",data)
   }

   function searchData(){
    // console.log("value of input : ", input);
   }

    return(
        <div className="search">
            <h2>Search your currency</h2>
            <div className="search-input">
                <input type='text'
                 placeholder="search your currency..."
                //  value={input}
                 onChange={(e)=>setInput(e.target.value)}
                 ></input>

               <Button 
               variant="contained" 
               color="secondary" 
               startIcon={<SearchIcon fontSize="large"/>}
               size='large'
               style={{textTransform:'lowercase',fontSize:'17px'}}
               onClick={searchData}
               >
                 search
               </Button>
            </div>
            <div className="search-result">
                {
                    data.filter((value)=>{

                        // console.log("name is ",value.name)
                        if(input==""){
                            return null
                        }else if (value.name.toLowerCase().includes(input.toLowerCase())){
                            return value
                        }
                    }).map((item,index)=>{
                        return(
                            <div className="search-coin-details" key={index}>
                                <span className="coin-name">
                                    <span className="img">
                                        <img src={item.icon} alt="icon"></img>
                                    </span>
                                    <span className="name">
                                        <Typography varient="p" fontSize="15px">
                                            {item.name}
                                        </Typography>
                                    </span>
                                </span>
                                <span className="coin-price">
                                    <Typography varient="p" fontSize="15px">
                                        $ {item.price.toFixed(2)}
                                    </Typography>
                                </span>
                                <span className="coin-1h">
                                    <Typography varient="p" color={item.priceChange1h>0?'rgb(13, 197, 74)':'red'} fontSize="15px">
                                        {item.priceChange1h}
                                    </Typography>
                                </span>
                                <span className="coin-1d" >
                                    <Typography varient="p" color={item.priceChange1h>0?'rgb(13, 197, 74)':'red'} fontSize="15px">
                                         {item.priceChange1d}
                                    </Typography>
                                </span>
                                <span className="coin-market">
                                    <Typography  varient="p" fontSize="15px">
                                         {item.marketCap.toFixed(2)}
                                    </Typography>
                                </span>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    )
}



export default Search;