import Bitcoin from './bitcoin-logo.png'
import Etherem from './etherem.png'
import Chainlink from './chainlink.png'
import Doge from './doge.png'
import Polygon from './polygon.png'
import Solona from './solana.png'
import Tether from './tether.png'
import Tron from './tron-.png'
import Xrp from './xrp.png'
import Shiba from './shiba.png'
import RequestIcon from './request.png'
import Ultra from './ultra.png'
import Nano from './nano.png'
import Hex from './hex.png';
import { motion } from "framer-motion"




function CoinMenu(){

    const logoList = [
        {   
            name:'Bitcoin',
            logo:Bitcoin
        },
        {
            name:'Etherem',
            logo:Etherem
        },
        {
            name:'Doge',
            logo:Doge
        },
        {
            name:'Shiba',
            logo:Shiba
        },
        {
            name:'Ultra',
            logo:Ultra
        },
        {
            name:'Nano',
            logo:Nano
        },
        {
            name:'Hex',
            logo:Hex
        },
        {
            name:'Tron',
            logo:Tron
        },
        {
            name:'RequestIcon',
            logo:RequestIcon
        },
        {
            name:'Polygon',
            logo:Polygon
        },
        {
            name:'Chainlink',
            logo:Chainlink
        },
        {
            name:'Tether',
            logo:Tether
        },
        {
            name:'Solona',
            logo:Solona
        },
    ]

    const logoBox = logoList.map((item,index)=>{
        return(
            <span key={index} className="coin-img">
                <img src={item.logo} alt={item.name}></img>
            </span>
        )
    })

    return(
        <div 
        className="coin-menu"
          
        >
            {logoBox}
        </div>
    )
}


export default CoinMenu;