import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import SettingsIcon from '@mui/icons-material/Settings';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import IconButton from '@mui/material/IconButton';
import { Link } from 'react-router-dom';
import { motion } from "framer-motion"


function NavBar(){

    return(
        <motion.div 
         className="navbar"
         initial={{opacity:0,scale:0}}
         whileInView={{opacity:1,scale:1}}
         transition={{duration:0.6}}
         viewport={{once:true}}
        >
            <a href="#">
                <IconButton>
                    <HomeIcon fontSize='medium' style={{color:'white'}}/>
                </IconButton>
            </a>
            <a href="#">
               
                    <IconButton>
                        <InfoIcon fontSize='medium' style={{color:'white'}}></InfoIcon>
                    </IconButton>
               
            </a>
            <a href="#">
                <IconButton>
                    <AccountCircleIcon fontSize='medium' style={{color:'white'}}></AccountCircleIcon>
                </IconButton>
            </a>
            {/* <a href="#">
                <IconButton>
                    <SettingsIcon fontSize='medium' style={{color:'white'}}></SettingsIcon>
                </IconButton>
            </a> */}
        </motion.div>

    )
}


export default NavBar;