import { Typography } from "@mui/material";
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import TwitterIcon from '@mui/icons-material/Twitter';
import InstagramIcon from '@mui/icons-material/Instagram';


function Footer(){

    return(
        <div className="footer">
            <div className="top-sec">
                <div className="footer-child sec-1">
                    <Typography gutterBottom variant="h5" fontWeight="bold">
                        Cryptonian
                    </Typography>
                    <Typography variant="subtitle2" color="#fbe2f4" width="80%">
                        Track your cryptocurrency investments with ease. Get real-time market data, alerts, and historical performance metrics for all your favorite coins in one portfolio.
                    </Typography>
                </div>
                <div className="footer-child sec-2">
                    <Typography gutterBottom variant="h6" fontWeight="400">
                        Links
                    </Typography>
                    <span className="site-link crypto-site">
                        <a href="https://prices.org/">Prices.org</a>
                        <a href="https://coinmarketcap.com/">CoinMarketCap</a>
                        <a href="https://www.cryptocompare.com/">CryptoCompare</a>
                        <a href="https://messari.io/">messari</a>
                        <a href="https://www.coingecko.com/">coingecko</a>
                    </span>
                </div>
                <div className="footer-child sec-3">
                    <Typography gutterBottom variant="h6" fontWeight="400">
                        Blog
                    </Typography>
                    <span className="site-link blog">
                        <a href="https://coinmarketcap.com/">CoinMarketCap</a>
                        <a href="https://dailycoin.com/">Daily Coin</a>
                        <a href="https://www.forbes.com/?sh=100d1f902254">Forbes</a>
                        <a href="https://cointelegraph.com/">CoinTelegraph</a>
                    </span>
                </div>
            </div>
            

            <div className="bottom-sec">
                <div className="sec-4 copyright">
                    <Typography variant="subtitle2" fontWeight="200">
                        copyright @2023 all right reserved.
                    </Typography>
                </div>
                <div className="sec-5 social">
                    <a href="https://github.com/itssouray">
                        <GitHubIcon fontSize="medium" />
                    </a>
                    <a href="https://www.linkedin.com/in/sourav-gupta-70b83a20b/">
                        <LinkedInIcon fontSize="medium" />
                    </a>
                    <a href="">
                        <TwitterIcon fontSize="medium" />
                    </a>
                    <a href="">
                        <InstagramIcon fontSize="medium"/>
                    </a>
                </div>
            </div>
        </div>
    )
}



export default Footer;