import { Card,  Typography } from "@mui/material";
import Bitcoin from './bitcoin-logo.png'
import Etherem from './etherem.png'
import BitCoinImg from './bitcoin-img.jpg'
import { motion } from "framer-motion";

import { useEffect, useState } from "react";


function CoinConainer(){
    const [coinList,setcoinList] = useState([]);

   

    useEffect(()=>{
        getCoinData()
    },[])

   async function getCoinData(){
    let result = await fetch('https://api.coinstats.app/public/v1/coins?skip=0&limit=8&currency=EUR')
                .then((resp) => {
                    return resp.json();
                })
    // console.log(result)
    setcoinList(result.coins)
    // console.log("data : ",coinList)
   }


    return(
        <motion.div 
            className="coin-container"
            initial={{opacity:0,scale:0}}
            whileInView={{opacity:1,scale:1}}
            transition={{duration:0.6}}
            viewport={{once:true}}
        >
            <div className="coin-container-head">
                <Typography gutterBottom variant="h3">Top Currency</Typography>
            </div>

            <div className="coin-conainer-bottom">
                       {
                        coinList.map((item,index)=>{

                            return(
                                <Card key={index} className="currency-container" style={{
                                    background: 'linear-gradient(135deg,rgba(255, 255, 255, 0.16),rgba(255, 255, 255, 0.041))',
                                    backdropFilter: 'blur(10px)',
                                    webkitBackdropFilter: 'blur(10px)',
                                    borderRadius:'10px',
                                    padding:'5px'
                                }}>
                                    <div className="coin-head">
                                        <span className="coin-head-img">
                                            <img src={item.icon} alt="coin-img"></img>
                                        </span>
                                        <span className="coin-head-name">
                                            <h3>{item.name}</h3>
                                            <h5 className="small-price">$ {item.price}</h5>
                                        </span> 
                                    </div>
        
                                    <div className="card-content">  
                                        <span className="price-in-days">
                                            <small>1 day</small>
                                             <Typography variant="subtitle2" color={item.priceChange1d>0?'rgb(13, 197, 74)':'red'}>
                                                {item.priceChange1d}
                                            </Typography>
                                        </span>
                                        <span className="price-in-days">
                                            <small>1 hour</small>

                                            <Typography variant="subtitle2" color={item.priceChange1h>0?'rgb(13, 197, 74)':'red'}>
                                                {item.priceChange1h}
                                            </Typography>
                                        </span>
                                        <span className="price-in-days">
                                            <small>1 week</small>

                                            <Typography variant="subtitle2" color={item.priceChange1w>0?'rgb(13, 197, 74)':'red'}>
                                                {item.priceChange1w}
                                            </Typography>
                                        </span>
                                    </div>

                                    <div className="social-cap">
                                        <span className="site">
                                            <small>Market Cap $ :</small>
                                            <h5 className="market-cap">
                                                {item.marketCap} 
                                            </h5>
                                        </span>
                                    </div>

                                </Card>
                            )
                        })
                     }  
            </div>

        </motion.div>
    )
}


export default CoinConainer;